import React from 'react';
import Category from './category';
import $ from 'jquery';
import Map from "./multimarker";
import Map1 from "./map";
//import places from "./place.json";
const axios = require('axios');

class App1 extends React.Component {
constructor(props)
{
  super(props);
  this.state = {
  isBack:false,
  isNext:false,
  isClick:false,
  couriername:[],
  product:[]
}
}

clickBooking=()=>{
this.setState({ isClick: true });
}

renderRedirect = (type) => {
if(type==='back') 
this.setState({ isBack: true,isNext:false });
else if(type==='next') 
this.setState({ isBack: false,isNext:true });
}

componentDidMount() {

axios.get("http://18.213.86.227:4000/common/ProductType/Courier")
.then((response) => {
return response.data;
})
.then(data => {
let teamsFromApi = data.map(team => { return {value: team.ProductType, display: team.ProductType} })
this.setState({ product: teamsFromApi });
}).catch(error => {
console.log(error);
});

axios.get("http://18.213.86.227:4000/courier/couriercompany/App")
.then((response) => {
return response.data;
})
.then(data => {
let teamsFromApi = data.map(team => { return {value: team.CourierName, display: team.CourierName} })
this.setState({ couriername: teamsFromApi });
}).catch(error => {
console.log(error);
});

$(function(){
window.$('.select2').select2();
window.$('form.material').materialForm(); // Apply material
window.$('form').validate({ 
errorPlacement: function(error, element) {}
}); // Apply validator with no error messages but classes only
});
};

render()
{
  let res;
  const places1 = [{
   "id": 1,
            "name": this.props.address,
            "latitude": this.props.fromlat,
            "longitude": this.props.fromlng
},{
   "id": 2,
   "name": this.props.address1,
            "latitude": this.props.tolat,
            "longitude": this.props.tolng
}];
console.log(places1)

  const isClick1=this.state.isClick;
  
  res = <div className="wrapper">
    <div className="">
      <div className="row">
        <div className="col-md-6 col-xs-12 text-center">

         <div className="card card-success card-outline">
          <div className="card-header">

            <h3 className="card-title text-center">
              <div className="row rowflexdisable">
                <div className="col-md-3 col-xs-6">
                  <button className="btn1 btn-4 btn-4b" style={{float:'left',fontWeight:'500'}} onClick={()=>this.renderRedirect('back')}><i className="fa fa-arrow-left">&nbsp;Back</i></button>
                </div>
                <div className="col-md-6 col-xs-6">
                  <span style={{marginTop:'10px',fontFamily:'fantacy',fontWeight:'500',fontSize:'28px',
                  background: 'linear-gradient(to right, darkviolet 0%, #f4511e 100%)',
                  WebkitBackgroundClip: 'text',
                  WebkitTextFillColor: 'transparent',marginLeft:'-10px' }}> Local </span>
                </div>
                <div className="col-md-3 col-xs-6">
                  <button className="btn1 btn-4 btn-4a" style={{float:'right',fontWeight:'500'}} onClick={()=>this.renderRedirect('next')}><i className="fa fa-arrow-right">&nbsp;Next</i></button>
                </div>
              </div>
            </h3>
          </div>

          <div className="card-body">
            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <button type="button" className="btn btn-success" style={{width: "70px"}}>From</button>
              </div>
              <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address }/>
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <button type="button" className="btn btn-danger" style={{width: "70px"}}>To</button>
              </div>
              <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address1 }/>
            </div> 
            
            <form action="" className="material">

              <div className="form-group">
                <label className="select2text">Product</label>
                <select className="form-control select2" style={{width: '100%'}} placeholder="Product">
                  <option key="select">Select Product</option>
                  {this.state.product.map((team) => <option key={team.value} value={team.value}>{team.display}</option>)}
                </select>
              </div>

              <input type="text" name="receivername" placeholder="Receiver Name" required />
              <input type="text" name="receiveradd1" placeholder="Receiver Address1" required />
              <input type="text" name="receiveradd2" placeholder="Receiver Address2" />
              <input type="text" name="receivermobno" placeholder="Receiver MobileNo" required />

              <div className="form-group">
                <label className="select2text">Number of Items</label>
                <select className="form-control" style={{width: '100%'}} placeholder="Number of Items">
                  <option key="select">Select No. of Items</option>
                  <option key="1">1</option>
                  <option key="2">2</option>
                  <option key="3">3</option>
                </select>
              </div>

              <textarea name="remarks" placeholder="Remarks"></textarea>

            </form>

          </div>
          <div className="row rowflexdisable" style={{paddingLeft:'20px',paddingRight:'30px'}}>
            <button class="button col-md-12 col-xs-12"><span>Go</span></button>
          </div>

        </div>

      </div>
      <div className="col-md-6 col-xs-12" style={{ height: '100vh', width: '100%' }}>
         <Map
    center={{ lat: this.props.fromlat, lng: this.props.fromlng }}
    zoom={8}
    places={places1}
  />
      </div>
    </div>

  </div>
</div>;

if(isClick1)
  return <Category />
else if(this.state.isBack)
{
  return <Map1
     google={this.props.google}
     center={{lat: this.props.fromlat, lng: this.props.fromlat}}
     height='600px'
     zoom={15}
     isType='courier'
     mapHeader='Courier'
     fromlat={this.props.fromlat}
     fromlng={this.props.fromlng}
     />
}
else if(this.state.isNext)
{
 return <Category />
}
else return (res);
}
}

export default App1;
// background: 'linear-gradient(to right, #30CFD0 0%, #330867 100%)',
//    WebkitBackgroundClip: 'text',
//    WebkitTextFillColor: 'transparent' 